<template>
  <div>
    <SwiperCompnents :isPagination="isPagination">
      <div class="swiper-slide" v-for="banner in bannerList" :key="banner.bannerId">
        <img style="width:100%;height:100%" :src="banner.imgUrl" alt />
      </div>
    </SwiperCompnents>
    <div class="cinemaMenuClass">
      <router-link v-for="cin in cinemaMenu" :key="cin.id" :to="cin.path">
        <p @click="MenuClick(cin.id)">{{cin.title}}</p>
      </router-link>
      <div :class="['tab-ink-bar-wrapper',activeClass]">
        <span class="tab-ink-bar" style="width: 56px;"></span>
      </div>
    </div>
    <router-view></router-view>
  </div>
</template>

<script>
import Swiper from "swiper";
import "swiper/css/swiper.min.css";
import {instance} from "@/utils/http";
import Vue from "vue";
import { Button } from "vant";
import { mapMutations,mapState } from 'vuex';

Vue.use(Button);
export default {
  data() {
    return {
      activeClass: false,
      cinemaMenu: [
        {
          id: 1,
          title: "正在热映",
          path: "nowplaying"
        },
        {
          id: 2,
          title: "即将上映",
          path: "comingsoon"
        }
      ],
      bannerList: [],
      isPagination: true
    };
  },
  components: {
    SwiperCompnents: () => import("@/components/swiperComponents")
  },
  methods: {
    ...mapMutations('TabbarStore',['changeIsTabbar']),
    MenuClick(index) {
      if(index===1){
        this.activeClass = 'tab-ink-bar-wrapper-activeA';
      }else{
        this.activeClass = 'tab-ink-bar-wrapper-activeB';
      }
    }
  },
  computed:{
    ...mapState('cityStore',['defaultCity','defaultCityId']),
  },
  created() {
    this.changeIsTabbar(true)
    instance(`/gateway?type=2&cityId=${this.defaultCityId}&k=635909`, {
      headers: {
        "X-Host": "mall.cfg.common-banner"
      }
    })
      .then(res => {
        this.bannerList = [
          {
            bannerId: 9999,
            imgUrl:
              "https://static.maizuo.com/v5/upload/4a8da56010356705571f85b345165ed8.jpg"
          },
          ...res.data.data
        ];
        this.$nextTick(() => {
          var mySwiper = new Swiper(".swiper-container", {
            loop: true, // 循环模式选项

            // 如果需要分页器
            pagination: {
              el: ".swiper-pagination"
            },

            // 如果需要前进后退按钮
            navigation: {
              nextEl: ".swiper-button-next",
              prevEl: ".swiper-button-prev"
            },

            // 如果需要滚动条
            scrollbar: {
              el: ".swiper-scrollbar"
            }
          });
        });
      })
      .catch(err => {
        console.log(err);
      });
  }
};
</script>
<style lang="scss">
.cinemaMenuClass {
  width: 100%;
  height: 8vh;
  display: flex;
  justify-content: space-around;
  align-items: center;
  border-bottom: 1px solid lightgray;
  box-sizing: border-box;
  position: relative;
  a {
    box-sizing: border-box;
    display: block;
    text-align: center;
    height: 8vh;
    line-height: 8vh;
    font-size: 14px;
    cursor: pointer;
    transition: 0.2s;
  }
  .router-link-active {
    // border-bottom: 1px solid #ff5f16;
    color: #ff5f16;
  }
  .tab-ink-bar-wrapper {
    position: absolute;
    margin: auto;
    bottom: -1px;
    left: 0;
    width: 50%;
    transition: transform 0.1s cubic-bezier(0.35, 0, 0.25, 1),
      -webkit-transform 0.1s cubic-bezier(0.35, 0, 0.25, 1);
    .tab-ink-bar {
      border-bottom: 2px solid #ff5f16;
      border-radius: 20px;
      display: block;
      margin: auto;
    }
  }
  .tab-ink-bar-wrapper-activeA {
    transform: translateX(0px);
  }
  .tab-ink-bar-wrapper-activeB {
    transform: translateX(100%);
  }
}
</style>